@media (min-width: 768px) {
	.section-menu.sticky{
		margin-bottom: 61px;
	}
}

.footer .container__inner{
	position: relative;
	padding-top: 120px;
	@media (max-width: 767px){
		padding-top: 60px;
	}
	&:before{
		position: absolute;
		top: 0;
		left: 0;
		height: 1px;
		width: 75%;
		background: $primary-color;
		content: "";
		display: inline-block;
	}
}
