@media (min-width: 768px){
	.btn-toggled{
		display: none;
	}
}

// 767
@media (max-width: $tablet-vt - 1) {
	//body
	body{
		transition: all 0.3s ease;
		position: relative;
		right: 0;
		padding-top: 60px;
	}
	body.toggled{
		right: 250px;
		transition: all 0.3s ease;
		overflow: hidden;
	}
	
	//header
	header{
		position: fixed;
		top:0;
		width: 100%;
		z-index: 999;
		background-color: $white-color;
		padding-top: 10px;
		padding-bottom: 10px;
		box-shadow: -1px 1px 1px $border-color;
	}
	body.toggled .block-main-menu{
		right: 0;
	}
	//header
	.region-header{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.block-info-header{
		display: none;
	}
	//section menu
	.section-menu{
		padding-top: 0;
		padding-bottom: 0;
		position: relative;
		z-index: 1001;
	}
}

