.region-header{
	text-align: center;
}
.block-logo{
	margin-bottom: 40px;
	position: relative;
	.sticky  & {
		@media (min-width: 768px) {
			position: fixed;
			top:15px;
			z-index: 1001;
			margin-bottom: 0;
			max-width: 140px;
		}
	}
	@media (max-width: 767px) {
		margin-bottom: 0;
		max-width: 140px;
	}
}
.block-info-company{
	font-family: $font-primary;
}

//slide
.block-slider{
	margin-left: -15px;
	margin-right: -15px;
	.slider-item__inner{
		padding-left: 15px;
		padding-right: 15px;
	}
}
.slider-item__inner{
	color: $primary-color;
	.slider-item__image{
		margin-bottom: 20px;
		overflow: hidden;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
		a{
			display: inline-block;
			position: relative;
			transition: 0.3s all ease;
			&:before{
				position: absolute;
				content: "";
				display: inline-block;
				top:0;
				right: 0;
				bottom:0;
				left: 0;
				z-index: 1;
			}
			img{
				transition: 0.3s all ease;
			}
			&:hover{
				&:before{
					background: rgba($white-color, 0.1);
				}
				img{
					transform: scale(1.1);
				}
			}
		}
	}
	.slider-item__category{
		text-transform: uppercase;
		font-size: 20px;
		font-family: $font-fourth;
		font-weight: 500;
		margin-bottom: 5px;
		@media (max-width: 991px){
			font-size: 16px;
		}
	}
	.slider-item__title{
		text-transform: uppercase;
		font-size: 30px;
		font-family: $font-third;
		font-weight: 300;
		margin-bottom: 10px;
		@media (max-width: 991px){
			font-size: 26px;
		}
		&:hover, &:focus{
			text-shadow: 1px 1px 1px;
		}
	}
}

//block-main-home
.block-main-home{
	.main-home_item{
		@media (max-width: 575px){
			margin-bottom: 50px;
		}
	}
	.main-home__image{
		margin-bottom: 20px;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
		a{
			display: inline-block;
			position: relative;
			transition: 0.3s all ease;
			overflow: hidden;
			&:before{
				position: absolute;
				content: "";
				display: inline-block;
				top:-20px;
				right: -20px;
				bottom:-20px;
				left: -20px;
				z-index: 1;
				border: 1px solid $border-color;
				transition: 0.3s all ease;
			}
			&:hover{
				&:before{
					background: rgba($white-color, 0.2);
					top:20px;
					right: 20px;
					bottom:20px;
					left: 20px;
				}
			}
		}
	}
	.main-home__category{
		text-transform: uppercase;
		font-size: 20px;
		font-family: $font-fourth;
		font-weight: 500;
		margin-bottom: 5px;
		@media (max-width: 991px){
			font-size: 16px;
		}
	}
	.main-home__title{
		text-transform: uppercase;
		font-size: 30px;
		font-family: $font-third;
		font-weight: 300;
		margin-bottom: 5px;
		@media (max-width: 991px){
			font-size: 26px;
		}
		&:hover, &:focus{
			text-shadow: 1px 1px 1px;
		}
	}
}

//block-info-header
.block-info-header{
	font-size: 18px;
	color: $primary-color;
	font-family: $font-primary;
}


//block-article-highlights
.block-article-highlights{
	margin-left: -15px;
	margin-right: -15px;
	display: inline-block;
	overflow: hidden;
	.article-highlights__image{
		padding-left: 15px;
		padding-right: 15px;
		width: 50%;
		float: left;
		@media (max-width: 991px) and (min-width: 576px){
			width: 40%;
		}
	}
	.article-highlights__title{
		font-size: 36px;
		text-transform: uppercase;
		padding-left: 15px;
		padding-right: 15px;
		width: 50%;
		display: inline-block;
		@media (max-width: 991px) and (min-width: 576px) {
			width: 60%;
		}
		@media (max-width: 991px){
			font-size: 30px;
		}
		@media (max-width: 575px){
			font-size: 26px;
		}
		a:hover,a:focus{
			text-decoration: underline;
		}
	}
	.article-highlights__body{
		font-family: $font-primary;
		color:$primary-color;
		line-height: 1.75;
		padding-left: 15px;
		padding-right: 15px;
		width: 50%;
		display: inline-block;
		@media (max-width: 991px) {
			width: 60%;
		}
		@media (max-width: 767px) {
			width: 100%;
			margin-top: 20px;
		}
	}
}

//block-library-img
.block-library-img{
	.library-img__image{
		@media (max-width: 575px){
			margin-bottom: 20px;
		}
	}
}

//block-info-footer
.block-info-footer{
	font-size: 18px;
	color: $primary-color;
	@media (max-width: 991px) {
		font-size: 16px;
	}
	.block-info-footer__title{
		font-family: $font-primary;
		margin-bottom: 30px;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
	}
	.info-footer__content__item{
		margin-bottom: 25px;
		display: flex;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
		> .icon{
			margin-right: 25px;
		}
	}
}

//block-basic-page
.block-basic-page{
	.block-basic-page__inner{
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-left: -15px;
		margin-right: -15px;
	}
	.title-page{
		width: 50%;
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 40px;
	}
	.block-basic-page__item{
		width: 50%;
		padding-left: 15px;
		padding-right: 15px;
		margin-bottom: 40px;
		@media (max-width: 767px){
			margin-bottom: 30px;
		}
		.block-basic-page__image{
			margin-bottom: 15px;
			@media (max-width: 767px){
				margin-bottom: 10px;
			}
			a{
				display: inline-block;
				position: relative;
				transition: 0.3s all ease;
				overflow: hidden;
				&:before{
					position: absolute;
					content: "";
					display: inline-block;
					top:-20px;
					right: -20px;
					bottom:-20px;
					left: -20px;
					z-index: 1;
					border: 1px solid $border-color;
					transition: 0.3s all ease;
				}
				&:hover{
					&:before{
						background: rgba($white-color, 0.2);
						top:20px;
						right: 20px;
						bottom:20px;
						left: 20px;
					}
				}
			}
		}
		.block-basic-page__title{
			font-family: $font-third;
			font-size: 30px;
			text-transform: uppercase;
			font-weight: 300;
			@media (max-width: 767px){
				font-size: 24px;
			}
			@media (max-width: 575px){
				font-size: 18px;
			}
		}
	}
}


//block-page-detail
.block-banner-page-detail{
	margin-bottom: 100px;
	position: relative;
	.title-page{
		position: absolute;
		top:0;
		left: 0;
		right: 0;
		bottom: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 55px;
		text-transform: uppercase;
		h1{
			font-weight: bold;
			color: $white-color;
		}
	}
}
.block-page-detail{
	.item-image-detail{
		margin-bottom: 30px;
	}
}
