
.main-news__title{
  padding-bottom: 50px;
}

.news-content__body{
  font-family: $body-font;
  font-size: 18px;
  line-height: 30px;
  color:$black-color;

}


.news-content__img img{
  max-width: 970px;
  height: auto;

}
.nav-page{
  float: right;
  padding-bottom: 100px;
}
.page-link{
  color: $black-color;
  font-family: iCiel Panton;
  font-weight: 500;
  font-size: 24px;
  line-height: 50px;
  border: 0;
}
.page-item.active .page-link{
  color: unset;
  background-color: unset;
  font-weight: 900;
}
.block-staff-item{
  .staff-item{
    display: flex;
    align-items: flex-start;
    &:nth-child(2n){
      flex-direction: row-reverse;
      align-items: flex-end;
      margin-top: -40px;
      margin-bottom: 40px;
      text-align: right;
      @media (max-width: 757px)and (min-width:480px){
      margin-top: 0;
      }
      @media (max-width: 480px){
        margin-top: 0;
      }

    }

  }

  .staff-img{
    width: 30%;
  }
  .staff-info {
    background-color: #333333;
    width: 70%;
    color: $white-color;
  }
  .staff-info h2 {
    text-transform: uppercase;
    font-family: $body-font;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 30px;
    letter-spacing: 0.5em;
    color: $white-color;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    @media (max-width: 757px)and (min-width:480px){
      font-size: 18px;
    }
    @media (max-width: 480px){
      font-size: 9px;
      padding-top: 0;
      margin-bottom: 0;

    }

  }
  .chuc-vu{
    font-family: $body-font;
    font-style: italic;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    letter-spacing: 0.5em;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    @media (max-width: 757px) and (min-width:480px){
      font-size: 13px;
    }
    @media only screen and (max-width: 480px){
      font-size: 8px;
      padding-bottom: 0;
    }
  }
}

#title-news{
  width: 50%;
}

.news__title h1{
  font-family: $body-font;
  font-style: normal;
  font-weight: normal;
  font-size: 50px;
  text-transform: uppercase;
  color: $black-color;
  @media (max-width: 480px) {
    font-size: 22px;
  }

}
//block-article-news
.block-article-news{
  margin-left: -15px;
  margin-right: -15px;
  display: inline-block;
  overflow: hidden;
  margin-bottom: 29px;
  .article-news__image{
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;
    float: left;
    @media (max-width: 991px) and (min-width: 576px){
      width: 40%;
    }

  }

}
.article-news__title{
  font-size: 36px;
  text-transform: uppercase;
  padding-left: 15px;
  padding-right: 15px;
  width: 50%;
  display: inline-block;
  @media (max-width: 991px) and (min-width: 576px) {
    width: 60%;
  }
  @media (max-width: 991px){
    font-size: 30px;
  }
  @media (max-width: 575px){
    font-size: 26px;
    padding-top: 10px;
    width: 100%;
  }


  a:hover,a:focus{
    text-decoration: underline;
  }
}
.article-news__body{
  font-family: $font-primary;
  color:$primary-color;
  line-height: 1.75;
  padding-left: 15px;
  padding-right: 15px;
  width: 50%;
  display: inline-block;
  @media (max-width: 991px) {
    width: 60%;
  }
  @media (max-width: 767px) {
    width: 100%;
    margin-top: 20px;
  }
}




