/*------------------------------------------------------------------*/
/* Typography */

html{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-text-size-adjust: 100%;
    font-size: 100%;
}

body {
    color: $body-color;
    font-family: $body-font;
    font-size: 16px;
    line-height: 1.42857143;
    font-weight: 400;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6{
    color:$black-color;
    margin-bottom: 15px;
}
h1, h2, h3,
.h1, .h2, .h3{
    line-height: 1.1;
    font-weight: 500;
    color: $body-color;
}

h1, .h1{
    font-size: 36px;
}
h2, .h2{
    font-size: 32px;
}
h3, .h3{
    font-size: 16px;
}
h4, .h4{
    font-size: 16px;
}
h5, .h5{
    font-size: 16px;
}
h6, .h6{
    font-size: 16px;
}

a {
    transition:all 0.3s ease-out;
    color: $primary-color;;
    outline: none;
    &:hover, &:focus {
        color:$body-color;
        outline: none;
        text-decoration: none;
    }
}

strong {
    font-weight: bold;
}

ul, ol {
    margin-bottom: 15px;
    padding-left:0;
    padding-right:0;
    margin-left:0;
    margin-right: 0;
}

ul{
    list-style: none;
}

p {
    margin-bottom: 10px;
}

label {
    display: block;
    margin-bottom: 5px;
}

img {
    max-width: 100%;
    height: auto;
}


input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition: "color 9999s ease-out, background-color 9999s ease-out";
    -webkit-transition-delay: 9999s;
}

input, select, textarea {
    width: 100%;
    max-width: 450px;
    padding:9px 15px;
    border:1px solid $border-color;
    outline: none;
    box-shadow:none;
    border-radius: 0;
    &:focus {
        border: 1px solid rgba($border-color, .6);
        outline: none;
    }
}



input[type="file"]{
    padding:9px 15px;
    line-height: 1.42857143;
}


input[type="submit"]{
    margin: 0px 10px 10px 0px;
    width: auto;
    padding:14px 60px 14px 30px;
    color: $black-color;
    background-color: $white-color;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 18px;
    border:none;
    font-weight: bold;
    &:last-child{
        margin-right: 0;
    }
}

input[type="radio"], input[type="checkbox"] {
    margin-top: -3px;
    margin-right: 3px;
    max-width: 100%;
    width: auto;
}


input[type="date"],input[type="time"]{
    line-height: 1.6;
    width: auto;
}

input[type="date"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-outer-spin-button{
  -webkit-appearance: none;
  margin: 0;
}

input[type="date"],input[type="time"]{
  -moz-appearance: textfield;
}


input[type="password"]{
    width: 100%;
}



blockquote{
    padding-left: 20px;
    border-left: 5px solid $border-color;
    font-style: italic;
}

select::-ms-expand {
    display: none;
}



textarea {
    border: 1px solid $border-color;
    max-width:100%;
    &:focus {
        border: 1px solid rgba($border-color, .6);
        outline: none;
    }

}

iframe {
    max-width: 100%;
}


table{
    width: 100%;
    td,th{
        padding:5px;
        border:1px solid $border-color;
    }
}

header{
    padding-top: 25px;
    padding-bottom: 22px;
}

section, footer, header{
    padding-top:60px;
    padding-bottom: 60px;
    @media (max-width: 1199px){
        padding-top:30px;
        padding-bottom: 30px;
    }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
   details{
        list-style-type:none;
   }
}










