.block-main-menu{
	font-family: $font-primary;
	font-size: 18px;
	padding-top: 10px;
	padding-bottom: 10px;
	transition: all 0.3s ease;
	@media (min-width: 768px){
		.menu-item a{
			position: relative;
			&:after{
				content: "";
				display: none;
				width: 0;
				height: 0;
				border-left: 6px solid transparent;
				border-right: 6px solid transparent;
				border-bottom: 5px solid $primary-color;
				position: absolute;
				bottom:-15px;
				left: 50%;
				transform: translateX(-50%);
			}
		}
		.menu-item.active a, .menu-item a:hover{
			&:after{
				display: inline-block;
			}
		}
	}
	.block-main-menu_inner{
		width: 100%;
		transition: all 0.3s ease;
		position: relative;
	}
	.menu-item-search{
		cursor: pointer;
	}
	.form-search{
		.form-search__inner{
			position: relative;
			@media (max-width: 767px){
				float: right;
				margin-right: 50px;
				margin-top: 17px;
				transition: all 0.3s ease;
				body.toggled &{
					margin-right: 300px;
				}
			}
		}
		.input-group-search{
			position: absolute;
			width: 300px;
			right: 0;
			top:calc(100% + 21px);
			display: none;
			box-shadow: 0px 4px 7px -1px $border-color;
			@media (max-width: 767px){
				right: -50px;
			}
		}
		&.toggled-search .input-group-search{
			display: flex;
		}
		@media (max-width: 767px){
			position: fixed;
			z-index: 1000;
			top:0;
			width: 100%;
			max-width: 540px;
			left: 50%;
			padding-left: 15px;
			padding-right: 15px;
			transform: translateX(-50%);
			height: 0;
		}
	}
	.navbar--padding{
		margin-bottom: 0;
		max-width: 900px;
		margin-left: auto;
		margin-right: auto;
		width: 100%;
		transition: margin-right 0.3s ease;
		@media (min-width: 768px){
			padding-left: 0;
			padding-right: 0;
		}
		@media (max-width: 767px) {
			flex-direction: column;
			padding-top: 30px;
			.menu-item{
				margin-bottom: 20px;
			}
		}
	}
	@media (max-width: 767px) {
		position: fixed;
		right: -250px;
		width: 250px;
		background-color: $primary-color;
		color: $white-color;
		height: 100vh;
		transition: all 0.3s ease;
		top:0;
		a{
			color: $white-color;
		}
	}
	&.sticky{
		@media (min-width: 768px) {
			position: fixed;
			width: 100%;
			top:0;
			background-color: $white-color;
			z-index: 1000;
			box-shadow: 0px 2px 3px -1px $border-color;
		}
		.block-main-menu_inner{
			padding-left: 200px;
			margin-right: 0;
			@media (max-width: 991px) {
				padding-left: 160px;
			}
			@media (max-width: 767px) {
				padding-left: 0;
			}
		}
	}
}

.btn-toggled{
	font-size: 26px;
}
.navbar-toggler--mobile{
	background-color: $black-color;
}

//block-main-menu--footer
.block-main-menu--footer{
	font-size: 18px;
	color: $primary-color;
	@media (max-width: 991px) {
		font-size: 16px;
	}
	@media (max-width: 575px){
		margin-bottom: 40px;
	}
	.block-main-menu--footer__title{
		font-family: $font-primary;
		margin-bottom: 30px;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
	}
	.menu-item{
		margin-bottom: 25px;
		@media (max-width: 575px){
			margin-bottom: 10px;
		}
	}
}
